import React from 'react';
//import MainStyle from '../../css/Layout/MainStyle';
import HomeStyle from '../../css/authentication/HomeStyle';
import '../../css/authentication/Home.css';

class Home extends React.Component{
    componentDidMount(){
        HomeStyle();
    }

    render(){
        return(
            <div id="home" className="home row">
                <div className="col-3 my-3 container d-flex flex-wrap align-content-center justify-content-center">
                    <a href={window.sessionStorage.path + "guest/"}> 
                        <button className="authBtn">
                            <i className="fas fa-universal-access"></i>
                        </button>
                    </a>
                </div>
                <div className="col-3 my-3 container d-flex flex-wrap align-content-center justify-content-center">
                    <a href={window.sessionStorage.path + "login/"}>
                            <button className="authBtn loginBtn focusBtn">
                                <i className="fas fa-lock"></i>
                            </button>
                    </a>
                </div>
                <div className="col-3 my-3 container d-flex flex-wrap align-content-center justify-content-center">
                    <a href={window.sessionStorage.path + "register/"}>
                        <button className="authBtn">
                            <i className="fas fa-user-plus"></i>
                        </button>
                    </a>
                </div>
            </div>
        );
    }
}

export default Home;